import React, { useEffect, useState, useMemo } from "react";
import WindowedSelect from "react-windowed-select";

// Debounce helper
const useDebounce = (value, delay = 300) => {
  const [debounced, setDebounced] = useState(value);

  useEffect(() => {
    const handler = setTimeout(() => setDebounced(value), delay);
    return () => clearTimeout(handler);
  }, [value, delay]);

  return debounced;
};

// Helper function to convert string to title case
const toTitleCase = (str) => {
  return str
    .toLowerCase()
    .split(" ")
    .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
    .join(" ");
};

const LocationSelect = ({ value, onChange, colClass = "col-sm-3" }) => {
  const [places, setPlaces] = useState([]);
  const [inputValue, setInputValue] = useState("");

  const debouncedInput = useDebounce(inputValue, 200); // 200ms debounce

  // Load places once
  useEffect(() => {
    const fetchPlaces = async () => {
      try {
        const res = await fetch("/Cities.json");
        if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
        const data = await res.json();
        setPlaces(data); // keep raw array
      } catch (err) {
        console.error("Failed to load places:", err);
      }
    };
    fetchPlaces();
  }, []);

  // Filtered and memoized options
  const filteredOptions = useMemo(() => {
    if (!debouncedInput)
      return places.slice(0, 500).map((p) => ({
        value: p,
        label: toTitleCase(p),
      }));

    return places
      .filter((p) => p.toLowerCase().includes(debouncedInput.toLowerCase()))
      .slice(0, 500) // limit to top 500 matches
      .map((p) => ({
        value: p,
        label: toTitleCase(p),
      }));
  }, [places, debouncedInput]);

  // Convert current value to title case for display
  const displayValue = useMemo(() => {
    return value ? { value, label: toTitleCase(value) } : null;
  }, [value]);

  return (
    <div className={`${colClass} col-xs-6`}>
      <label className="form-label mb-2 font-16">Search By District</label>
      <WindowedSelect
        options={filteredOptions}
        value={displayValue}
        onChange={(opt) => onChange(opt ? opt.value : "")}
        onInputChange={setInputValue} // track user input
        isClearable
        isSearchable
        placeholder="Search district..."
        menuPlacement="auto"
        windowThreshold={100} // virtualize after 100 items
        menuPortalTarget={document.body}
        styles={{
          control: (baseStyles) => ({
            ...baseStyles,
            minHeight: "55px",
            zIndex: 5,
          }),
          menuPortal: (base) => ({
            ...base,
            zIndex: 99, // âœ… ensure it stays above everything
          }),
        }}
      />
    </div>
  );
};

export default LocationSelect;
